// Main Stylesheet

// Colors
$primary-color          : #d74600;
$secondary-color        : #ffcbcb;
$accent-color           : #6555ff;
$shade-color            : #789572;
$complementary-color    : #2f2f2f;

// Fonts
$body-font        : DINPro, sans-serif;
$heading-font     : DINPro, sans-serif;
$body-font-size   : 1.1rem;
$base-line-height : 1.8;


@import
  "_resets",
  "_utility",
  "_universal",
  "_mixins",
  "_buttons",
  "_nav",
  "_footer",
  "_header",
  "_media-queries";

.intro {
  height: 100vh;
}

.intro video {
  height: 100%;
  width:100%;
  object-fit: cover;
}